* {
  /* font-size: 100px; */
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Noto Sans", sans-serif;
}

li {
  list-style: none;
}
a {
  text-decoration: none;
}

.navbar {
  background: linear-gradient(
    90deg,
    rgb(110, 94, 254) 0%,
    rgba(73, 63, 252, 1) 100%
  );
  height: 80px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  /* z-index: 2; */
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
}

.menu-icon {
  margin-right: 20px;
  color: #fff;
  font-size: 25px;
  display: none;
  cursor: pointer;
}
.nav-menu {
  display: none;
  z-index: 1;
}
.nav-menu.active {
  display: flex;
  /* justify-content: space-around; */
}

.nav-menu.active li {
  padding: 10px;
}

.nav-links {
  color: #fff;
  /* margin: 0; */
}

.nav-links:hover {
  background-color: #6d76f7;
  border-radius: 4px;
  transition: all 0.5s ease-out;
}

.nav-links-mobile {
  display: none;
}

.btn {
  margin: 0;
}

@media screen and (max-width: 960px) {
  .navbar {
    position: relative;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
  }
  .nav-menu.active {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 100%;
    height: 350px;
    position: absolute;
    left: 0;
    top: 80px;
    color: #fff;
    background-color: #6668f4;
  }

  .nav-menu.active li {
    text-align: center;
  }

  .nav-links:hover {
    background-color: #7577fa;
    border-radius: 0;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    background-color: #4ad9ef;
    color: #fff;
    width: 80%;
    /* height: 100%; */
    padding: 1.5rem;
    border-radius: 4px;
    margin: 0 auto;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #6568f4;
    transition: 250ms;
  }
  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    transform: translate(25%, 50%);
  }
  Button {
    display: none;
  }
  /* 컴포넌트에 css먹히네 */
}
