:root {
  --primary: #3acbf7;
}

.btn {
  padding: 8px 20px;
  border-radius: 4px;
  outline: none;
  border: none;
  cursor: pointer;
  color: #fff;
  margin: 0 auto;
}
.btn:hover {
  transition: all 0.5s ease-out;
  background-color: #fff;
  color: #6568f4;
}
.btn--primary {
  background-color: var(--primary);
}
.btn--outline {
  background-color: transparent;
  color: #fff;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}
.btn--medium {
  font-size: 18px;
}
.btn--large {
  padding: 12px 26px;
  border-radius: 4px;
  font-size: 20px;
}
